import AdminLayout from "@/layouts/Admin/index.vue";
import BotosRowPlaceHolder from "@/components/PlaceHolders/BotosRowPlaceHolder/index.vue";
import { Boto } from "@/interfaces/Boto";
import { defineComponent } from "vue";
import routes from "@/routes";
import Toggle from "@vueform/toggle";
import { useStore } from "vuex";
import { useRoute } from "vue-router";
import BotoModal from "@/components/BotoModal/index.vue";
import { HttpRequestMethod } from "@/enums/HttpRequestMethod";
import HttpClient from "@/util/HttpClient";
import { v4 as uuidv4 } from "uuid";

const items: Boto[] = [];

export default defineComponent({
  name: "Botos",
  components: {
    AdminLayout,
    BotosRowPlaceHolder,
    Toggle,
    BotoModal,
  },
  setup() {
    const store = useStore();
    const route = useRoute();
    let bot = null;
    if (route.params.botId) {
      bot = {
        id: route.params.botId,
        name: route.params.botName,
        frequency: route.params.botFrequency,
        schedule: route.params.botSchedule,
        frequency_unit: route.params.botFrequencyUnit,
      };
    }
    return {
      store,
      bot,
    };
  },
  data() {
    return {
      context: process.env.VUE_APP_CONTEXT,
      items,
      isLoadingData: false,
      errorText: "",
      error: false,
      activationModal: false,
      deletionModal: false,
      deletionLoading: false,
      activeItem: null as any,
      search: "",
      filteredItems: items,
      uid: uuidv4(),
    };
  },
  computed: {
    filteredList(): any {
      return this.items.filter((item: { name: string }) =>
        item.name.toLowerCase().includes(this.search.toLowerCase())
      );
    },
    activationRunningText(): string {
      if (this.bot && this.bot.schedule === "SCH") {
        const frequencyWithUnit = this.getFrequencyWithUnit(this.bot);
        return `run every ${frequencyWithUnit}`;
      } else {
        return "trigger with every event";
      }
    },
  },
  created() {
    if (!navigator.onLine) {
      routes.push({ name: "home.offline", params: { route: this.$route.fullPath } });
    }
  },
  async mounted() {
    // Get Blocks
    try {
      if (this.bot) {
        this.activationModal = true;
      }
      this.isLoadingData = true;
      await this.loadBotos();
    } catch (e) {
      this.error = true;
      this.errorText = e as string;
    } finally {
      this.isLoadingData = false;
    }
  },
  methods: {
    async loadBotos() {
      const botos = await this.getBotos();
      this.items = botos;
    },
    getFrequencyWithUnit(bot: any) {
      let unit = bot.frequency_unit || "MIN";
      let frequency;
      // TODO: enum
      switch (unit) {
        case "HR":
          frequency = bot.frequency / 60;
          unit = "hours";
          break;
        case "DAY":
          frequency = bot.frequency / 24 / 60;
          unit = "days";
          break;
        case "MIN":
          frequency = bot.frequency;
          unit = "minutes";
          break;
        default:
          frequency = bot.frequency;
          unit = "minutes";
      }
      return `${frequency} ${unit}`;
    },
    closeActivatedBotMessage() {
      this.activationModal = false;
    },
    cancelDeleteBot() {
      this.deletionModal = false;
      this.activeItem = null;
    },
    async deleteBoto() {
      try {
        this.deletionLoading = true;
        const item = this.getItemById(this.activeItem);
        const response = await HttpClient.request(
          `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${item!.id}/`,
          this.store.getters.localStorageAuthToken!,
          HttpRequestMethod.DELETE,
          true
        );
        this.deletionModal = false;
        this.deletionLoading = false;
        this.activeItem = null;
        this.isLoadingData = true;
        await this.loadBotos();
        this.isLoadingData = false;
      } catch (e) {
        // TODO
        console.error(e);
        this.deletionLoading = false;
        this.activeItem = null;
        this.deletionModal = false;
        this.isLoadingData = false;
      }
    },
    async getBotos() {
      return await HttpClient.request(
        `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/my/bots/`,
        this.store.getters.localStorageAuthToken!,
        HttpRequestMethod.GET
      );
    },
    async switchBoto(id: number) {
      try {
        const item = this.getItemById(id);
        const status = item!.status;
        const name = item!.name;
        const formdata = new FormData();
        formdata.append("name", name);
        formdata.append("status", status);
        formdata.append("frequency", item!.frequency.toString());
        await HttpClient.request(
          `https://${process.env.VUE_APP_BOTO_API_DOMAIN}/api/bot/${item!.id}/`,
          this.store.getters.localStorageAuthToken!,
          HttpRequestMethod.PUT,
          formdata,
          true
        );
      } catch (e) {
        // TODO
        console.error(e);
      }
    },
    deleteBotoDialog(id: number) {
      this.deletionModal = true;
      this.activeItem = id;
    },
    gotoBoto(id: number) {
      routes.push({ name: "home.editor", params: { id, uuid: uuidv4() } });
    },
    getItemById(id: number) {
      return this.items.find((o: Boto) => o.id === id);
    },
    copyBoto(id: number) {
      const item = this.getItemById(id);
      item!.copy = true;
      item!.name = `${item!.name} copy`;
      this.store.commit("setTemplate", JSON.stringify(item));
      const v = uuidv4();
      // TODO: cache issues
      window.location.href = `${this.context}editor?${v}&copy=true`;
    },
  },
});
